var fs = "// Calculate standard normalized difference\nfloat normalized_difference_calc(vec4 image) {\n  return ((image.r - image.g) / (image.r + image.g));\n}\n";
export default {
  name: "normalized_difference",
  fs: fs,
  inject: {
    "fs:DECKGL_MUTATE_COLOR": "\n    image = vec4(normalized_difference_calc(image), 0., 0., 0.);\n    "
  }
};